import {
	Tabs,
	HeaderPage,
	GeneralInfo,
	ApplicantInfo,
} from "Components/Appraisal";

const tabs = [
	{
		title : "InformaciÃ³n General del Inmueble",
		Tab   : GeneralInfo,
	},
	{
		title : "Datos del Solicitante",
		Tab   : ApplicantInfo,
	},
];

const AppraisalRequest = ({
	delegations : {
		hookForm,
		onSubmit,
		isLoading,
	},
}) => {

	const { formState : { isDirty } } = hookForm;

	return (
		<>
			<HeaderPage
				currentIndex={1}
				section="header"
				isDirty={isDirty}
				onSubmit={onSubmit}
				isSubmitingData={isLoading}
			/>

			<Tabs
				tabs={tabs}
				onSubmit={onSubmit}
				hookForm={hookForm}
				isSubmitingData={isLoading}
			/>
		</>
	);
};

export default AppraisalRequest;
