import { useForm }   from "react-hook-form";
import { useParams } from "react-router-dom";

import { useHandlerMutation }      from "Helpers/Hooks";
import { AppraisalHOCPageFetcher } from "Components/Appraisal";
import AppraisalRequest            from "./AppraisalRequest";

const PARAMS = { select_fields : ["general_information", "applicant"] };
const DEFAULT_FETCH_DATA = { general_information : {}, applicant : {} };

const AppraisalRequestContainer = ({ appraise = DEFAULT_FETCH_DATA}) => {

	const { id } = useParams();

	// ------------> RTK-Q <---------------
	const [ editApprasial, { isLoading : isLoadingMutation } ] = useHandlerMutation({ toastName : "apprasialCRUD" });

	const { general_information, applicant } = appraise;

	// ------------> useHookForm <---------------
	const getValidZipCode = (zip_code) => typeof(zip_code) === "number" ? zip_code.toString() : zip_code;

	const defaultValues = {
		general_information : {
			...general_information,
			zip_code : getValidZipCode(general_information?.zip_code),
			state    : general_information?.state?.label ?? "",
		},
		applicant : { //Example: RFC: "MELM8305281H0", CURP : "ROVI490617HSPDSS05"
			...applicant,
			zip_code : getValidZipCode(applicant?.zip_code),
		},
	};

	const hookForm = useForm({ defaultValues });

	const { handleSubmit } = hookForm;

	// ------------> submitData <---------------
	const onSubmit = handleSubmit(async (data) => {
		await editApprasial({
			module : `/appraisals/request-appraise/${id}`,
			method : "PUT",
			data   : {
				...data,
				applicant : {
					...data.applicant,
				},
				general_information : {
					...data.general_information,
					state : general_information?.state,
				},
			},
			tags : ["appraisals", "apprasial_details"],
		});
	});

	return (
		<AppraisalRequest
			delegations={{
				hookForm,
				onSubmit,
				isLoading : isLoadingMutation,
			}}
		/>
	);
};
export default AppraisalHOCPageFetcher(AppraisalRequestContainer, PARAMS);

